import React from "react";
import Content from "./component/Content";
import MenuTree from "./component/MenuTree";
import Modal from "./component/Modal";
import axios from "axios";

const { useState } = React;

function App() {
  const [currentTopicWords, setCurrentTopicWords] = useState([]);
  const [currentTopic, setCurrentTopic] = useState("design and innovation");
  const [currentWord, setCurrentWord] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const showWordDefinitions = (word) => {
    // open link in new tab
    window.open(`https://dictionary.cambridge.org/dictionary/english/${word}`);

    // axios.get(`http://localhost:3001/${word}`).then(({ data }) => {
    //   if (data.name !== "Error") {
    //     setCurrentWord(data);
    //     setShowModal(!showModal);
    //   }
    // });
  };

  const itemClick = (key, value) => {
    setCurrentTopic(key);
    setCurrentTopicWords(value);
  };

  return (
    <div className="flex flex-row w-screen h-screen overflow-hidden">
      <MenuTree itemClick={itemClick} currentTopic={currentTopic} />
      <Content
        data={currentTopicWords}
        showWordDefinitions={showWordDefinitions}
      />
      <Modal
        data={currentWord}
        showModal={showModal}
        toggleModal={setShowModal}
      />
    </div>
  );
}

export default App;
