import React from "react";
import JSONDATA from "../static/IELTS-topics.json";
import Logo from "../static/icons/english-language-code.svg";

let icons = [
  "lightbulb-flash",
  "kakao-talk",
  "plant",
  "github",
  "run",
  "goblet",
  "mental-health",
  "car",
  "space-ship",
  "building",
  "money-dollar-box",
  "palette",
  "oil",
  "leaf",
  "book",
  "facebook",
  "global",
  "earth",
  "user-2",
  "building-2",
];

function MenuTree(props) {
  React.useEffect(() => {
    props.itemClick("design and innovation", JSONDATA["design and innovation"]);
  }, []);
  return (
    <div className="border-r w-64 px-4 opacity-90 overflow-y-auto scrollbar-hide">
      <h1 className="flex p-3 pb-2">
        <img src={Logo} alt="logo" className="h-14" />
        <span className="ml-2 font-bold text-4xl self-center">IELTS</span>
      </h1>
      <ul className="p-0">
        {Object.entries(JSONDATA).map(([key, value], index) => {
          return (
            <li
              key={key}
              className={`block cursor-pointer p-3 rounded-md content-center ${
                key === props.currentTopic ? "bg-gray-200" : ""
              }`}
              onClick={props.itemClick.bind(this, key, value)}
            >
              <div className="flex">
                <i className={`mr-2 ri-${icons[index]}-fill`}></i>
                <span className="text-md font-bold">{key}</span>
              </div>
            </li>
          );
        })}
      </ul>
    </div>
  );
}

export default MenuTree;
