import React from "react";

function Content({ data, showWordDefinitions }) {
  return (
    <div className="flex flex-1 flex-col bg-gray-200 opacity-90 overflow-auto scrollbar-hide">
      {Object.entries(data).map(([key, value], index) => {
        return (
          <div
            key={key}
            className={`flex flex-col border-b p-4"
            }`}
          >
            <div className="flex flex-row justify-between p-4">
              <span className="font-bold text-xl">{key}</span>
              <span className="text-sm text-gray-600">
                {value.length} words
              </span>
            </div>
            <div className="flex flex-row flex-wrap">
              {value.map((item, index) => {
                return (
                  item && (
                    <div
                      key={index}
                      onClick={showWordDefinitions.bind(this, item)}
                      className="flex flex-col border rounded-md border-blue-400 bg-blue-50 px-3 py-2 m-2 text-blue-500 cursor-pointer"
                    >
                      {item}
                    </div>
                  )
                );
              })}
            </div>
          </div>
        );
      })}
    </div>
  );
}

export default Content;
